import * as React from "react";
import { useNavigate } from "react-router-dom";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Button from "@mui/material/Button";
import { useContext } from "react";
import { ThemeContext } from "../context/Theme_context";
import stls from "../../styles/components/Headers.module.sass";
import { useTranslation } from "react-i18next";
import {
  ClickAwayListener,
  Divider,
  Drawer,
  Grow,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Popper,
} from "@mui/material";
import ArrowCircleLeftOutlinedIcon from "@mui/icons-material/ArrowCircleLeftOutlined";
import TipsAndUpdatesOutlinedIcon from "@mui/icons-material/TipsAndUpdatesOutlined";
import DisplaySettingsIcon from "@mui/icons-material/DisplaySettings";
import { HeaderContext } from "../context/Header_context";
import Brightness4OutlinedIcon from "@mui/icons-material/Brightness4Outlined";
import Brightness7OutlinedIcon from "@mui/icons-material/Brightness7Outlined";

export function Header() {
  const anchorRef = React.useRef<HTMLDivElement>(null);

  const navigate = useNavigate();

  const { t } = useTranslation();

  const { colors, toggleLanguage, currentTheme, toggleTheme, language } =
    useContext(ThemeContext);
  const { navItemsPage } = useContext(HeaderContext);

  const drawerWidth = 240;

  const drawerItems = [
    // {id: 1, name: t('drawer.myHome'), icon: <TipsAndUpdatesOutlinedIcon sx={{color: colors.zeta}} />, link: 'my_room'},
    {
      id: 2,
      name: t("drawer.settings"),
      icon: <DisplaySettingsIcon sx={{ color: colors.zeta }} />,
      link: "settings",
    },
  ];

  const langs = [
    { sysname: "ru", name: "" },
    { sysname: "en", name: "English" },
    { sysname: "hr", name: "Hrvatski" },
  ];

  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [lang, setLang] = React.useState(langs.filter(el => el.sysname === language)[0] || { sysname: "ru", name: "" });
  const [open, setOpen] = React.useState(false);

  React.useEffect(() => {
    if (language) {
      setLang(langs.filter(el => el.sysname === language)[0])
    }
  }, [language]);


  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };
  const handleClose = (event: Event) => {
    setOpen(false);
  };

  const list = () => (
    <Box
      sx={{ width: drawerWidth }}
      role="presentation"
      onClick={handleDrawerToggle}
      onKeyDown={handleDrawerToggle}
    >
      <List>
        {drawerItems.map((el: any) => (
          <ListItem key={el.id} disablePadding>
            <ListItemButton onClick={() => navigate(el.link)}>
              <ListItemIcon>{el.icon}</ListItemIcon>
              <ListItemText primary={el.name} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider />
      <List></List>
    </Box>
  );

  return (
    <AppBar sx={{ position: "static" }}>
      <Toolbar>
        <IconButton
          id="appbar"
          color="inherit"
          aria-label="open drawer"
          edge="start"
          onMouseEnter={handleDrawerToggle}
        >
          <MenuIcon />
        </IconButton>
        <Drawer open={mobileOpen} onClose={handleDrawerToggle}>
          <div
            style={{
              background: colors.beta,
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <ListItemButton
              style={{
                fontWeight: "bold",
                fontSize: "24px",
                color: colors.iota,
                display: "flex",
                justifyContent: "center",
              }}
              onClick={() => {
                navigate("/");
                handleDrawerToggle();
              }}
            >
              rinakz
            </ListItemButton>
            <IconButton onClick={handleDrawerToggle}>
              <ArrowCircleLeftOutlinedIcon sx={{ color: colors.iota }} />
            </IconButton>
          </div>
          {list()}
        </Drawer>
        <div onClick={() => navigate("/")} className={stls.buttonHome}>
          rinakz
        </div>
        <Box sx={{ display: { xs: "none", sm: "block" } }}>
          {navItemsPage.map((item: any) => (
            <a key={item.id} href={item.link ? `../${item.link}` : ""}>
              <Button id={`header_pages${item.id}`} sx={{ color: colors.iota }}>
                {item.name}
              </Button>
            </a>
          ))}
        </Box>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Button sx={{ color: colors.iota }}>
            {currentTheme == "dark" ? (
              <Brightness4OutlinedIcon onClick={() => toggleTheme("light")} />
            ) : (
              <Brightness7OutlinedIcon onClick={() => toggleTheme("dark")} />
            )}
          </Button>

          <div ref={anchorRef}>
            <Button onClick={handleToggle} sx={{ color: colors.iota }}>
              {lang.name}
            </Button>
          </div>
          <Popper
            style={{ zIndex: 1000 }}
            onClick={() => setOpen(false)}
            open={open}
            anchorEl={anchorRef.current}
            role={undefined}
            transition
          >
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                style={{
                  transformOrigin:
                    placement === "bottom" ? "center top" : "center bottom",
                }}
              >
                <List component="nav" sx={{ background: colors.alpha }}>
                  <div>
                    <ClickAwayListener onClickAway={handleClose}>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          gap: "12px",
                          padding: "12px",
                        }}
                      >
                        {langs.map((el) => (
                          <ListItem key={el.sysname} disablePadding>
                            <ListItemButton
                              onClick={() => {
                                setLang(el);
                                toggleLanguage(el.sysname);
                              }}
                            >
                              {el.name}
                            </ListItemButton>
                          </ListItem>
                        ))}
                      </div>
                    </ClickAwayListener>
                  </div>
                </List>
              </Grow>
            )}
          </Popper>
        </Box>
      </Toolbar>
    </AppBar>
  );
}
