import React, { useContext } from "react";
import { ThemeContext } from "../context/Theme_context";
import Work from "./stories/Work";
import Croatia from "./stories/Croatia";
import Doggy from "./stories/Doggy";
import Kanal from "./stories/Kanal";
import stls from "../../styles/components/AboutMe.module.sass";

export function StoryPage({ story }: any) {
  const { colors } = useContext(ThemeContext);

  const renderComponent = () => {
    let tsx;
    switch (story.alt) {
      case "Work":
        tsx = <Work />;
        break;
      case "Croatia":
        tsx = <Croatia />;
        break;
      case "Shiba":
        tsx = <Doggy />;
        break;
      case "Kanal":
        tsx = <Kanal />;
        break;
    }
    return tsx;
  };

  return (
    <div className={stls.storyPage}>
      <div className={stls.storyPageTitle}>
        <h1 style={{ textAlign: "end", marginRight: "20px" }}>{story.text}</h1>
        <img
          className={stls.storiesPhoto}
          style={{
            bottom: "-64px",
            width: "600px",
            height: "auto",
            position: "relative",
          }}
          alt={story.alt}
          src={story.ava}
        />
      </div>
      <div
        className={stls.storyContainer}
        style={{
          background: colors.kappa,
          color: colors.lambda,
        }}
      >
        {renderComponent()}
      </div>
    </div>
  );
}
