import React, { useContext, useEffect,  } from 'react';
import { useTranslation } from 'react-i18next';





const AppContext = React.createContext<any>(null);

function AppProvider(props: any) {
  const { t } = useTranslation();

    const stories = [
        {id: 1, ava: '/Apple.jpeg', alt: 'Work', path: '/work', text: t("story.work")},
        {id: 3, ava: '/Kanal.jpeg', alt: 'Kanal', path: '/kanal',  text: t("story.kanal")},
        {id: 4, ava: '/Doggy.jpeg', alt: 'Shiba', path: '/shiba',  text: t("story.shiba")},
        {id: 5, ava: '/Croatia.jpeg', alt: 'Croatia', path: '/croatia',  text: t("story.croatia")},
      ]







  return (
    <AppContext.Provider
      value={{
        stories,
  
      }}
      {...props}
    />
  );
}

export { AppProvider, AppContext };


